import axios from 'axios'
import router from '../router'
export const service = axios.create({})
service.defaults.baseURL = 'http://120.78.79.228'//baseURL url /api http://127.0.0.1:3000, axios.get('/test') === axios.get('http://127.0.0.1:3000/test')
service.defaults.timeout = 5000;
service.defaults.withCredentials = true

// request interceptor
service.interceptors.request.use(
    config => {
      //token
      config.params = {
        timestamp: Date.parse(new Date()),
        ...config.params
      }
      return config
    },
    error => {
      console.log(error) // for debug
      Promise.reject(error)
    }
  )

  //
service.interceptors.response.use(response => {
    return response
}, err => {
    console.log(':', err.response.status)
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.response.message = ''
          break;
        case 401:
          err.response.message = ''
          break;
        case 403:
          err.response.message = ''
          break;
        case 404:
          err.response.message = ','
          break;
        case 405:
          err.response.message = ''
          break;
        case 408:
          err.response.message = ''
          break;
        case 500:
          err.response.message = ''
          // router.replace('/index')// router.push  history    history 
          break;
        case 501:
          err.response.message = ''
          break;
        case 502:
          err.response.message = ''
          break;
        case 503:
          err.response.message = ''
          break;
        case 504:
          err.response.message = ''
          break;
        case 505:
          err.response.message = 'http'
          break;
        default:
          err.response.message = `${err.response.status}`
      }
    } else {
      err.response.message = ""
    }
      console.log('err:', err.response)
      return Promise.resolve(err.response)
})



